.store {
  width: 98%;
  display: grid;
  background-color: rgba(252, 252, 252, 0.671);
  grid-template-columns: repeat(3, 1fr);

  gap: 20px;
  padding: 10px;
}

.item {
  background-color: rgba(255, 255, 255, 0.736);
  border-radius: 16px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  gap: 30px;
  /* border: 30 px; */
  padding: 20px;
  text-align: center;
  padding: 50px;
  border-radius: 60px;
}

button {
  width: 100%; /* תאימו לרוחב ה-grid */
  min-height: 50px; /* גובה מינימלי */
  padding: 15px; /* הגדלת הפנימה לגובה ורוחב נוספים */
  font-size: 18px; /* טקסט גדול יותר */
  box-sizing: border-box; /* לוודא שה-padding מחושב בתוך הרוחב */
  background-color: #00ffaecb;
  color: white;
  border: none;
  cursor: pointer;
  min-height: 60px; /* גובה מינימלי נוסף לכפתור */
  font-size: 20px; /* טקסט גדול יותר לכפתור */
}

@media (max-width: 900px) {
  .store {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .store {
    grid-template-columns: 1fr;
  }
}
